* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  box-sizing: border-box;
}

.Page_Wrapper {
  background: url(/static/media/bookshelf.778c3941.jpg);
}

.App {
  text-align: center;
  height: 100vh;
  width: 1200px;

  position: relative;
  left: calc(50% - 600px);
}

.App_Content {
  display: flex;
  justify-content: space-between;
}

.Header {
  Background-color: lightblue;
  border-bottom: 6px solid navy;
  color: navy;
  padding-top: 8px;
  letter-spacing: 8px;
  font-family: 'Special Elite', cursive;
}

.Add_Form {
  padding: 15px;
  display: flex;
  flex-flow: column;
  text-align: left;

  background: rgba(135, 206, 250, 0.8);
}

.Add_Form label {
  display: flex;
  justify-content: space-between;
}

.Add_Form label span {
  font-size: 1.3em;
}

.Add_Form input[type=submit] {
  height: 40px;
  width: 100px;

  align-self: center;
}

#Text_Box {
  display: inline-block !important;
}

.Display {
  flex-grow: 3;
  background: skyblue;
}

.Book_Listing {
  display: flex;
  flex-flow: column;
}

.Book_Listing {
  margin: 20px;
  padding: 3px;
  border: 3px inset black;
  width: 300px;

  -webkit-transition-property: background-color; /* Safari */
  -webkit-transition-duration: 1s; /* Safari */
  transition-property: background-color;
  transition-duration: 1s;
}

.Book_Listing:hover {
  background-color: rgb(221, 224, 253)
}
